import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { MyCustomOrg } from "app/pages/tzb/custom/custom-group/my-custom-org/my-custom-org.component";
import { MyOrgChange } from "app/pages/tzb/custom/custom-group/my-custom-org/my-org-change/my-org-change.component";
import { MyOrgHistory } from "app/pages/tzb/custom/custom-group/my-custom-org/my-org-history/my-org-history.component";
export const routes: Routes = [
    {
        path: '',
        component: MyCustomOrg,
        children: [
            { path: 'my-org-change', component: MyOrgChange },
            { path: 'my-org-history', component: MyOrgHistory },
        ]
    },
]
export const MyCustomOrgRouting: ModuleWithProviders = RouterModule.forChild(routes);