import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol } from "app/@uisftech/common/service/validator";
import { Message } from "primeng/primeng";
import * as _ from 'lodash';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CALENDAR_CN } from "app/pages/tzb/workflow/activiti-work-flow/common/constant/config";
import { Router } from "@angular/router";
@Component({
    selector: 'my-org-history',
    templateUrl: './my-org-history.component.html',
    styleUrls: ['./my-org-history.component.scss'],
})
export class MyOrgHistory implements OnInit {
    msgs: Message[] = [];
    userform: FormGroup;
    message: any = { msg: [] };
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    code: any;
    first: number = 0;//分页控制
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//每页条数
    totalNum: number;//总条数
    dataList: any = [];
    statuIdList: any;//任务状态
    operationTypeList: any;//录入类别
    orgTreeDisplay: boolean = false;//模态框
    ch: any = CALENDAR_CN;//日期控件
    orgTree: any;//机构判别
    constructor(
        public httpService: CusOperationHttpService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public router: Router,
    ) {
        this.userform = fb.group({
            groupId: ['', specialSymbol],//群号
            ownerId: ['', specialSymbol],//群主
            orgId: [''],//原机构
            afterOrgId: [''],//调整后机构
            operationType: [''],//录入类别
            operationDateTimeStart: [''],//经办时间
            operationDateTimeEnd: [''],//经办时间
        })
    }
    ngOnInit() {
        this.codeValues() //调用方法，获取全部码值
        this.statuIdList = this.code['statuIdGroup'];
        this.operationTypeList = this.code['operationTypeGroup'];
        this.queryClick();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // 选择机构/选择员工
    openTree(i) {
        this.orgTreeDisplay = !this.orgTreeDisplay;
        this.orgTree = i;
    }
    // 关闭-机构模态框
    contacttreeCall(param) {
        this.orgTreeDisplay = false;
        if (this.orgTree == '1') {
            this.userform.get('orgId').setValue(param.orgId);
        } else if (this.orgTree == '2') {
            this.userform.get('afterOrgId').setValue(param.orgId);
        }
    }
    // 开始、结束日期判断
    showTime(param1, param2) {
        if (param1 == 'plan') {
            const operationDateTimeStart = this.commfunc.transDateN(this.userform.value.operationDateTimeStart);
            const operationDateTimeEnd = this.commfunc.transDateN(this.userform.value.operationDateTimeEnd);
            if (operationDateTimeStart && operationDateTimeEnd) {
                if (operationDateTimeStart > operationDateTimeEnd) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.userform.value.operationDateTimeStart = null;
                        } else {
                            this.userform.value.planContactTimeTo = null;
                        }
                    })
                }
            }
        }
    }
    // 手动输入日期
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }
    //查询
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //列表查询
    queryClick() {
        const param = Object.assign({}, this.userform.value);
        param['pageSize'] = this.pageSize;
        param['pageNum'] = this.pageNum;
        param['operationDateTimeStart'] = this.commfunc.transDateN(this.userform.value.operationDateTimeStart);
        param['operationDateTimeEnd'] = this.commfunc.transDateN(this.userform.value.operationDateTimeEnd);
        this.httpService.adjustmentGroupOrgQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList = data.resultList ? data.resultList : [];
                this.totalNum = data.total;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
            } else {
                this.dataList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 重置
    reset() {
        this.userform.get('groupId').setValue('');
        this.userform.get('ownerId').setValue('');
        this.userform.get('orgId').setValue('');
        this.userform.get('afterOrgId').setValue('');
        this.userform.get('operationType').setValue('');
        this.userform.get('operationDateTimeStart').setValue('');
        this.userform.get('operationDateTimeEnd').setValue('');
    }
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.queryClick();
    }
    //返回群列表
    returnGroup() {
        this.router.navigate(['/pages/tzb/custom/custom-group/my-custom-group']);
    }
}