import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { specialSymbol } from "app/@uisftech/common/service/validator";
import { Message } from "primeng/primeng";
import * as _ from 'lodash';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Router } from "@angular/router";
@Component({
    selector: 'my-org-change',
    templateUrl: './my-org-change.component.html',
    styleUrls: ['./my-org-change.component.scss'],
})
export class MyOrgChange implements OnInit {
    @Output() outValue = new EventEmitter();
    msgs: Message[] = [];
    userform: FormGroup;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    code: any;
    adjustmentOrgList: any = [];//机构集合
    orgTreeDisplay: boolean = false;//模态框
    single: boolean = false;//手工录入
    batch: boolean = false;//模板导入
    files: any = []; // 文件
    fileList: any = [];
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
    UPLOAD: string = TZB_CUS_UPLOAD;// 上传
    today = new Date();//日期
    operationType: any;//类型
    user: any;//用户信息
    fileUrl: any;//文件路径
    fileName: any;//文件名称
    groupSet: any = [];//群号集合
    dataList: any = [];//集合
    dealPerShow: boolean = false;
    waitSelUserList: any;
    outVal: any;
    inValue_data: any = 'tak';
    constructor(
        public httpService: CusOperationHttpService,
        private commonFunc: Customtools,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public router: Router,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.userform = fb.group({
            partyClassificationGroupId: [''],//群号
            orgName: [''],//所属机构
            orgId: [''],//所属机构
            groupName: [''],//群名称
            ownerName: [''],//群主
            ownerId: [''],//群主
            afterOrgId: ['', Validators.required],//所属机构
        })
    }
    ngOnInit() {
        this.operationType = '1';
        this.singlebatch();
    }
    // 选择机构
    openTree(param) {
        this.orgTreeDisplay = !this.orgTreeDisplay;
    }
    // 关闭-机构模态框
    contacttreeCall(param) {
        // console.log(param);
        this.orgTreeDisplay = false;
        this.userform.get('afterOrgId').setValue(param.orgId);
    }
    //群号事件
    partydetail() {
        const param = {};
        param['groupId'] = this.userform.value.partyClassificationGroupId;
        this.httpService.selectCustGroupInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.userform.get('groupName').setValue(data.groupName);
                this.userform.get('ownerName').setValue(data.ownerName);
                this.userform.get('ownerId').setValue(data.ownerId);
                this.userform.get('orgId').setValue(data.orgId);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //调整类型
    singlebatch() {
        if (this.operationType == '1') {
            this.single = true;
            this.batch = false;
            this.reset();
            this.adjustmentOrgList = [];
        } else if (this.operationType == '2') {
            this.single = false;
            this.batch = true;
            this.adjustmentOrgList = [];
        }
    }
    // 添加
    add() {
        if (!this.userform.value.partyClassificationGroupId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入群号' });
            return
        }
        if (!this.userform.value.afterOrgId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入调整后群所属机构' });
            return
        }
        let tempArr1 = [];
        let tempArr2 = {};
        tempArr1 = this.adjustmentOrgList;
        for (let e = 0; e < tempArr1.length; e++) {
            if (this.userform.value.partyClassificationGroupId == tempArr1[e].partyClassificationGroupId) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '手工录入群号重复' });
                return
            }
        }
        if (this.userform.value.afterOrgId == this.userform.value.orgId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '原归属机构与调整后归属机构相同' });
            return
        }
        tempArr2 = {
            partyClassificationGroupId: this.userform.value.partyClassificationGroupId,
            groupName: this.userform.value.groupName,
            ownerName: this.userform.value.ownerName,
            ownerId: this.userform.value.ownerId,
            orgId: this.userform.value.orgId,
            afterOrgId: this.userform.value.afterOrgId,
        }
        tempArr1.push(tempArr2);
        this.adjustmentOrgList = _.clone(tempArr1);
        this.reset();
    }
    //删除
    delete(i) {
        this.adjustmentOrgList.splice(i, 1);
        let tmpArr = _.clone(this.adjustmentOrgList);
        this.adjustmentOrgList = tmpArr;
    }
    // 提交
    submit() {
        if (this.adjustmentOrgList.length == '0') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '没有数据无法提交!' });
            return
        }
        this.waitSelUserList = this.commfunc.query('KHQ_QJGPLTZ', {});
        if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            let perPageShow = this.waitSelUserList.display;
            if (perPageShow == true) {
                this.dealPerShow = true;
            } else {
                this.dataList = [];
                this.groupSet = [];
                const param = {};
                let list = this.adjustmentOrgList;
                this.adjustmentOrgList.forEach(element => {
                    let obj = {};
                    let sub = {};
                    sub = element.partyClassificationGroupId;
                    obj['groupId'] = element.partyClassificationGroupId;
                    obj['groupName'] = element.groupName;
                    obj['ownerId'] = element.ownerId;
                    obj['ownerName'] = element.ownerName;
                    obj['orgId'] = element.orgId;
                    obj['afterOrgId'] = element.afterOrgId;
                    this.groupSet.push(sub);
                    this.dataList.push(obj);
                });
                param['groupSet'] = this.groupSet;
                param['operationType'] = this.operationType;
                param['operationOrg'] = this.user.extParam.orgId;
                param['operationTellerNo'] = this.user.userId;
                param['adjustmentOrgList'] = this.dataList;
                param['operationDateTime'] = this.commfunc.transDateN(this.today);
                this.httpService.batchAdjustmentOfGroupOrg(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.adjustmentOrgList = [];
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功！' }];
                    } else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '提交失败！';
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
        }
    }
    outDisplayCall(event) {
        this.dealPerShow = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';
        this.outVal.forEach(item => {
            ss += item.tellerId;
            specifyNextNode = item.userBank;
        });
        this.dataList = [];
        this.groupSet = [];
        const param = {};
        let list = this.adjustmentOrgList;
        this.adjustmentOrgList.forEach(element => {
            let obj = {};
            let sub = {};
            sub = element.partyClassificationGroupId;
            obj['groupId'] = element.partyClassificationGroupId;
            obj['groupName'] = element.groupName;
            obj['ownerId'] = element.ownerId;
            obj['ownerName'] = element.ownerName;
            obj['orgId'] = element.orgId;
            obj['afterOrgId'] = element.afterOrgId;
            this.groupSet.push(sub);
            this.dataList.push(obj);
        });
        param['authUser'] = ss;
        param['author'] = specifyNextNode
        param['groupSet'] = this.groupSet;
        param['operationType'] = this.operationType;
        param['operationOrg'] = this.user.extParam.orgId;
        param['operationTellerNo'] = this.user.userId;
        param['adjustmentOrgList'] = this.dataList;
        param['operationDateTime'] = this.commfunc.transDateN(this.today);
        this.httpService.batchAdjustmentOfGroupOrg(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.adjustmentOrgList = [];
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功！' }];
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '提交失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 按模板-文件上传
    load(event) {
        for (let file of event.files) {
            this.files = [];
            this.files.push(file)
        }
        if (event.xhr.response) {
            this.fileList = JSON.parse(event.xhr.response);
            if (this.fileList.success == false) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
                this.files = [];
                return
            }
        }
        if (this.fileList[0]) {
            this.fileUrl = this.fileList[0].fileUrl;
            this.fileName = this.fileList[0].fileName;
        }
        this.analysis();
    }
    //文件解析
    analysis() {
        const param = {};
        param['fileUrl'] = this.fileUrl;
        param['fileName'] = this.fileName;
        this.httpService.analysisImportExcelResult(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.adjustmentOrgList = data.resultList;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '解析成功！' }];
            } else {
                this.adjustmentOrgList = [];
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '解析失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    // 重置
    reset() {
        this.userform.get('partyClassificationGroupId').setValue('');
        this.userform.get('afterOrgId').setValue('');
        this.userform.get('groupName').setValue('');
        this.userform.get('ownerName').setValue('');
        this.userform.get('orgId').setValue('');
    }
    //返回群列表
    returnGroup() {
        this.router.navigate(['/pages/tzb/custom/custom-group/my-custom-group']);
    }
}