import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'

@Component({
    selector: 'my-custom-org',
    templateUrl: './my-custom-org.component.html',
    styleUrls: ['./my-custom-org.component.scss']
})
export class MyCustomOrg implements OnInit {
    show: number = 1;
    constructor(
        public commfunc: Commfunc,
        public router: Router,
    ) {
    }
    ngOnInit() {
        let stu_1 = 1; // 群批量机构调整
        let stu_2 = 2; // 群机构批量调整历史
        if (stu_1) {
            this.show = 1;
            this.router.navigate(['/pages/tzb/custom/custom-group/my-custom-org/my-org-change'])
        } else if (stu_2) {
            this.show = 2;
            this.router.navigate(['/pages/tzb/custom/custom-group/my-custom-org/my-org-history'])
        }
    }
    ngOnChanges() {
    }
    // 切换标签
    allot(num) {
        this.show = num;
    }
}